html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

body {
  margin: 0;
  font-size: 14px;
  background-color: #FFFBD7; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit; }

.hidden {
  display: none; }

.content-wrapper {
  max-width: 1080px;
  margin: 0 auto; }

.global-header {
  background-color: #FFFAC2;
  position: relative; }

.global-header .logo-container,
.global-header .header-navigation-container {
  display: inline-block; }

.global-header .logo-container {
  margin-bottom: 10px; }

.global-header .header-navigation-container {
  float: right; }

.global-header img {
  margin-top: 20px; }

.global-header .top-nav {
  width: 170%;
  background-color: #66584D;
  color: #fff;
  padding-left: 20px;
  float: right; }

.global-header .top-nav li {
  padding: 10px 20px 5px; }

.global-header .top-nav .donate {
  background-color: #FBB03B;
  padding-bottom: 10px;
  position: absolute;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  text-transform: uppercase;
  color: #66584D;
  font-weight: 600;
  box-shadow: 5px 5px 10px -2px;
  padding-top: 15px;
  margin-left: 20px; }

.global-header .top-nav a,
.global-header .top-nav a:visited {
  color: #fff; }

.site-nav {
  margin-top: 30px;
  margin-left: 4%;
  font-style: italic;
  font-size: 1.1em; }

.body {
  background-color: #9EA2B9; }

.main {
  padding-bottom: 3%; }

.main > .content {
  margin: auto;
  background-color: #fff;
  padding: 0 10px 10px; }

.main .slideshow img {
  width: 100%; }

.owl-item.loading {
  background: url(img/owl-carousel/AjaxLoader.gif) no-repeat center center; }

.owl-carousel .owl-wrapper,
.owl-carousel .owl-item {
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  position: relative; }

/* color pallette for carousel description backgrounds */
.main .owl-carousel .owl-item:nth-child(1) .featured-description {
  background-color: #D2B9FE; }

.main .owl-carousel .owl-item:nth-child(2) .featured-description {
  background-color: #F0A058; }

.main .owl-carousel .owl-item:nth-child(3) .featured-description {
  background-color: #60B6F5; }

.main .owl-carousel .owl-item:nth-child(4) .featured-description {
  background-color: #C3AE8D; }

/* include repeat styles in case more than 4 slides are added */
.main .owl-carousel .owl-item:nth-child(5) .featured-description {
  background-color: #D2B9FE; }

.main .owl-carousel .owl-item:nth-child(6) .featured-description {
  background-color: #F0A058; }

.main .owl-carousel .owl-item:nth-child(7) .featured-description {
  background-color: #60B6F5; }

.main .owl-carousel .owl-item:nth-child(8) .featured-description {
  background-color: #C3AE8D; }

.main .owl-carousel .featured-description {
  position: absolute;
  bottom: 10%;
  z-index: 10;
  cursor: pointer;
  width: 50%;
  padding: 20px;
  color: #544A65; }

.main .owl-carousel .featured-header {
  text-transform: uppercase;
  font-size: 1.5em;
  margin-bottom: 10px; }

.main .owl-carousel .featured-tagline {
  font-size: 1.5em;
  font-style: italic;
  color: #fff;
  margin-bottom: 10px; }

.main .owl-carousel .featured-learn-more:after {
  content: "";
  position: absolute;
  height: 0;
  width: 0;
  border: 5px solid transparent;
  border-left-color: #544A65;
  bottom: 23px;
  margin-left: 10px; }

.main .featured {
  margin-top: 15px; }

.main .featured img:first-child {
  margin-right: 2%; }

.main .featured img:last-child {
  margin-left: 2%; }

.main .featured img {
  width: 31.5%; }

.global-footer {
  border-top: 15px solid #3D4573;
  background-color: #FFFBD7; }

.global-footer nav {
  display: inline-block;
  color: #62544A; }

nav.footer-nav > span:after {
  content: " |";
  margin: 0 2px;
  font-size: 1.2em; }

nav.footer-nav > span:last-child:after {
  content: "";
  margin: 0;
  font-size: 1em; }
